@import './node_modules/normalize.css/normalize.css';

@font-face {
  font-family: DINPro;
  src: url('https://3xyh3sqxv063a8xzo5uk2zn1-wpengine.netdna-ssl.com/wp-content/themes/Andela/assets/fonts/FF-DIN-Pro.woff2')
      format('woff2'),
    url('https://3xyh3sqxv063a8xzo5uk2zn1-wpengine.netdna-ssl.com/wp-content/themes/Andela/assets/fonts/FF-DIN-Pro.woff')
      format('woff'),
    url('https://3xyh3sqxv063a8xzo5uk2zn1-wpengine.netdna-ssl.com/wp-content/themes/Andela/assets/fonts/FF-DIN-Pro.otf')
      format('opentype');
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: DINPro;
  src: url('https://3xyh3sqxv063a8xzo5uk2zn1-wpengine.netdna-ssl.com/wp-content/themes/Andela/assets/fonts/DINPro-Bold.eot');
  src: url('https://3xyh3sqxv063a8xzo5uk2zn1-wpengine.netdna-ssl.com/wp-content/themes/Andela/assets/fonts/DINPro-Bold.eot?#iefix')
      format('embedded-opentype'),
    url('https://3xyh3sqxv063a8xzo5uk2zn1-wpengine.netdna-ssl.com/wp-content/themes/Andela/assets/fonts/DINPro-Bold.woff')
      format('woff'),
    url('https://3xyh3sqxv063a8xzo5uk2zn1-wpengine.netdna-ssl.com/wp-content/themes/Andela/assets/fonts/DINPro-Bold.ttf')
      format('truetype'),
    url('https://3xyh3sqxv063a8xzo5uk2zn1-wpengine.netdna-ssl.com/wp-content/themes/Andela/assets/fonts/DINPro-Bold.svg#DINRegular')
      format('svg');
  font-weight: 500;
  font-style: bold;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
small,
input,
ul,
li,
select,
option,
div,
section {
  font-family: DINPro;
}

h1 {
  font-size: 2.488rem;
}

h2 {
  font-size: 2.074rem;
}

h3 {
  font-size: 1.728rem;
}

h4 {
  font-size: 1.44rem;
}

h5 {
  font-size: 1.22rem;
}

p {
  font-size: 1rem;
}

small {
  font-size: 0.833rem;
}

/************************
 *  CUSTOM STYLING
 ************************/
#banner {
  background-image: url('./assets/andela-notion-bg.jpg');
  background-size: cover;
  width: 100%;
  height: 30vh;
  margin-bottom: 4rem;
}

#hero h1 {
  color: #4d4d4d;
}

#hero h4 {
  font-weight: 400;
  color: #9b9a98;
}

.content {
  width: 50vw;
  margin: 0 auto;
  margin-bottom: 4rem;
}

.content h4 {
  color: #0b22a7;
  text-transform: capitalize;
}
